// import { Popover } from "antd";
import React, { useEffect, useState } from "react";
import { useHoverDelay, useLongPress } from "./hooks";
import Tooltip from "rc-tooltip";
export default function EmojiGroup(props) {
  const { data, onSelect } = props;
  return (
    <div className="emoji_container">
      {data.map((emoji) => {
        return (
          <EmojiItem
            key={emoji.name}
            data={emoji}
            onSelect={onSelect}
          ></EmojiItem>
        );
      })}
    </div>
  );
}

function EmojiItem(props) {
  const { data, onSelect } = props;
  const [open, setOpen] = useState(false);
  const [isHoverPopover, setIsHoverPopover] = useState(false);

  const longPressHandler = useLongPress();
  const { startHoverDelay, cleanup } = useHoverDelay();
  function handleSelectEmoji(data, skinUnicode = []) {
    const unicode = [...data.unicode, ...skinUnicode].map((code) =>
      code.replace("U+", "0x")
    );
    const result = String.fromCodePoint(...unicode);

    console.log({
      unicode: data.unicode,
      htmlCode: data.htmlCode,
      skinUnicode,
      result,
    }); // 0x1F435

    onSelect(result);
    setOpen(false);
    setIsHoverPopover(false);
  }

  const Content = (emoji) => {
    // emoji.htmlSkin
    const { htmlCode, htmlSkin, unicodeSkin } = emoji;
    if (htmlSkin) {
      return (
        <div
          className="emoji_skin_group"
          onMouseOver={() => {
            setIsHoverPopover(true);
          }}
          onMouseLeave={() => {
            setIsHoverPopover(false);
          }}
        >
          {Object.keys(htmlSkin).map((key) => {
            const value = htmlSkin[key];
            const skinUnicode = unicodeSkin[key];
            return (
              <div
                key={key}
                className="emoji_item"
                dangerouslySetInnerHTML={{ __html: htmlCode.join("") + value }}
                onClick={() => {
                  handleSelectEmoji(emoji, [skinUnicode]);
                }}
              ></div>
            );
          })}
        </div>
      );
    } else {
      return null;
    }
  };

  return (
    <Tooltip
      visible={open || isHoverPopover}
      showArrow={false}
      overlay={Content(data)}
      placement={"top"}
      getTooltipContainer={(node) => node.parentElement}
      zIndex={999}
      styles={{
        root: {
          opacity: 1,
          background: "transparent",
        },
        body: {
          border: "1px solid #f1f4f7",
          borderRadius: "10px",
          padding: "4px",
        },
      }}
    >
      <div
        className={`emoji_item ${open ? "emoji_active" : ""}`}
        dangerouslySetInnerHTML={{ __html: data.htmlCode.join("") }}
        onClick={(e) => {
          // e.target.classList.add("emoji_active");
          // setTimeout(() => {
          //   e.target.classList.remove("emoji_active");
          // }, 200);
          handleSelectEmoji(data);
        }}
        onMouseEnter={(e) => {
          if (data.htmlSkin) {
            startHoverDelay(e, 500, () => {
              console.log("onMouseEnter 500");
              setOpen(true);
            });
          }
        }}
        onMouseLeave={() => {
          if (data.htmlSkin) {
            console.log("onMouseLeave");
            cleanup();
            setTimeout(() => {
              setOpen(false);
            }, 100);
          }
        }}
        onTouchStart={(e) => {
          if (data.htmlSkin) {
            console.log("onTouchStart");
            longPressHandler(e, 500, () => {
              setOpen(true);
            });
          }
        }}
      ></div>
    </Tooltip>
  );
}
